/**
 * Header
 */
.site-header {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
  height: 65vh;
  position: relative;
}
.site-header .navbar {
  min-height: 25%;
  background: linear-gradient(to bottom, black 0%, rgba(153, 153, 153, 0) 60%, rgba(255, 255, 255, 0) 100%);
}
.site-header .navbar a {
  color: #fff;
}
.site-header .headerInner {
  height: 100%;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(128, 128, 128, 0) 70%, black 100%);
}
.site-header .headerInner .title-container {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.brand {
  font-weight: bold;
}

.navbar-brand {
  background: #fff;
  max-width: 15.833rem;
  padding: 1.778rem;
  border-bottom-left-radius: 0.556rem;
  border-bottom-right-radius: 0.556rem;
}

.navbar-collapse {
  margin-top: -50px;
}
.navbar-collapse li {
  padding-left: 15px;
  padding-right: 15px;
}
.navbar-collapse li a {
  text-decoration: none;
  position: relative;
  color: #fefefe;
}
.navbar-collapse li a::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #daa05b;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}
.navbar-collapse li a:hover {
  color: #fefefe;
}
.navbar-collapse li a:hover::before {
  transform: scaleX(1);
}

.navbar-collapse.collapse.show {
  margin-top: 0;
  background: #212529;
  z-index: 1000;
  padding-left: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.navbar-collapse.collapse.show li {
  margin-bottom: 1.5rem;
  padding-left: 0;
  padding-right: 0;
}
.navbar-collapse.collapse.show li a {
  color: #fff;
  text-decoration: underline;
}

footer.page-footer {
  font-size: 1rem;
}
footer.page-footer div[itemprop=address] {
  font-size: 0.833rem;
}
footer.page-footer div[itemprop=address] span[itemprop=telephone] {
  display: block;
}

#menu-footer li {
  display: inline-block;
  border-right: 1px solid #fff;
}
#menu-footer li:last-child {
  border-right: none;
}
#menu-footer li a {
  color: #fff;
  text-decoration: none;
}

.socials li a {
  font-size: 30px;
}

.inner-slide em, .inner-slide strong, .inner-slide cite {
  display: block;
}
.inner-slide cite {
  font-size: 6.867rem;
  font-style: normal;
  font-weight: 700;
  font-family: "PT Serif";
}
@media (max-width: 1199.98px) {
  .inner-slide cite {
    font-size: 70px;
  }
}
.inner-slide strong {
  font-weight: 400;
  font-family: "PT Serif";
  color: #daa05b;
  font-size: 2.746125rem;
}
@media (max-width: 1199.98px) {
  .inner-slide strong {
    font-size: 2.197125rem;
  }
}
.inner-slide em {
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 1;
  font-size: 4.291875rem;
  font-family: "PT Serif";
}
@media (max-width: 1199.98px) {
  .inner-slide em {
    font-size: 50px;
  }
}
.inner-slide .btn {
  border-radius: 2.222rem;
  color: #fff;
}

